*{
    margin: 0;
    padding: 0;
}
.Nav-Background{
    background-color: rgb(102, 102, 245);
    color: white;
    border-bottom: 1px solid rgb(123, 123, 255);
}
.left-nav{
    border-radius: 15px;
}
.img1 img{
    height: 35px;
    width: 35px;
    margin-right: 13px;
    cursor: pointer;
}
.img img{
    height: 25px;
    width: 25px;
    border: 2px solid black;
    border-radius: 50%;
    cursor: pointer;
}

.right-nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 20px;
}
.right-nav svg{
    margin-right: 13px;
    color: white;
    background-color: white;
    height: 23px;
    width: 23px;
    border-radius: 10px;
    cursor: pointer;
}
