*{
    margin: 0;
    padding: 0;
}
.second-nav{
    padding-top: 25px;
    display: flex;
    justify-content: space-between;
    height: 200px;
    background-color: rgb(102, 102, 245);
}
.second-nav .left{
    margin-left: 20px;
    display: flex;
    width: 300px;
}

.left .left-table{
    display: flex;
    align-items: center;
    height: 33px;
    width: 33px;
    font-size: 23px;
    margin-right: 20px;
    cursor: pointer;
}
.left .right-table svg{
    height: 22px;
    width: 22px;
    margin: 0 5px 0 15px;
    cursor: pointer;
}
.left .right-table{
    border-radius: 5px;
    background-color: rgb(225, 228, 246);
    display: flex;
    align-items: center;
    margin-left: 30px;
    height: 33px;
    width: 203px;
}

.left .right-table span{
    padding: 0 6px 0 6px;
    font-size: 17px;
    font-weight: bold;
    color: rgb(131, 131, 238);
    cursor: pointer;

}

.right{
    width: 200px;
}

.right span{
    background-color: rgb(225, 228, 246);
    margin-left: 10px;
    cursor: pointer;
    padding: 3px 6px 3px 6px;
    border-radius: 5px;
    font-size: 17px;
    font-weight: 600;
    color: rgb(131, 131, 238);
}



@media screen and (max-width:800px){
    .second-nav{
        display: block;
    }
    .second-nav .right{
        margin: 10px 0 100px 10px;
    }
}